import questions from "../questions";
import { resetData } from "../resetData";

const resetGame = () => {
  let result = {
    score: [0, 0],
    data: questions
  };

  for (let i = 0; i < resetData.length; i++) {
    for (let j = 0; j < resetData[i].team.length; j++) {
      if (resetData[i].team[j] - 1 === 0) {
        result.data[i].questions[j].team = 1;
        result.data[i].questions[j].isEnabled = false;
        result.score[0] +=
          resetData[i].scor[j] * result.data[i].questions[j].score;
      }
      if (resetData[i].team[j] - 1 === 1) {
        result.data[i].questions[j].team = 2;
        result.data[i].questions[j].isEnabled = false;
        result.score[1] +=
          resetData[i].scor[j] * result.data[i].questions[j].score;
      }
    }
  }
  return result;
};

export default resetGame;
