import "./styles.css";
import React, { useState, useEffect } from "react";
import { makeStyles } from "@material-ui/styles";
import resetGame from "./common/resetGameFunctin";
import GameBaord from "./Componenets/GameBoard";
import QPage from "./Componenets/QPage";
export default function App() {
  const classes = useStyles();
  const [gameData, setGameData] = useState(null);
  const [viewBoard, setViewBoard] = useState(true);
  const [teamsScore, setTeamsScore] = useState(null);
  const [categoryIndex, setCategoryIndex] = useState(null);
  const [questionIndex, setQuestionIndex] = useState(null);
  const handleQCardClick = (cIndex, qIndex) => {
    setViewBoard(false);
    setCategoryIndex(cIndex);
    setQuestionIndex(qIndex);
    let tempGameData = gameData;
    tempGameData[cIndex].questions[qIndex].isEnabled = false;
    setGameData(tempGameData);
  };
  const handleQuestionClose = (val) => {
    setViewBoard(val);
  };
  const handleUpdateTeamScore = (teamIndex, multiBy) => {
    let tempTeamScore = teamsScore;
    tempTeamScore[teamIndex] +=
      gameData[categoryIndex].questions[questionIndex].score * multiBy;
    setTeamsScore(tempTeamScore);
    let tempGameData = gameData;
    tempGameData[categoryIndex].questions[questionIndex].team = 1;
    setGameData(tempGameData);
    setViewBoard(true);
  };
  useEffect(() => {
    let result = resetGame();
    setGameData(result.data);
    setTeamsScore(result.score);
  }, []);
  if ((gameData != null) & (teamsScore != null)) {
    return (
      <div className={classes.root}>
        {viewBoard ? (
          <GameBaord handleClick={handleQCardClick} data={gameData} />
        ) : (
          <QPage
            handleClose={handleQuestionClose}
            categoryStr={gameData[categoryIndex].catagory}
            questionStr={
              gameData[categoryIndex].questions[questionIndex].qDescription
            }
            questionColor={
              gameData[categoryIndex].questions[questionIndex].color
            }
            handleCorrectAnswer={handleUpdateTeamScore}
          />
        )}
        <div className={classes.scoresRoot}>
          <div className={classes.teamScoreContainer}>
            <div>קבוצה 1</div>
            <div>{teamsScore[0]}</div>
          </div>
          <div className={classes.teamScoreContainer}>
            <div>קבוצה 2</div>
            <div>{teamsScore[1]}</div>
            <div />
          </div>
        </div>
      </div>
    );
  } else {
    return <div></div>;
  }
}

const useStyles = makeStyles({
  root: {
    backgroundColor: "AliceBlue",
    height: "100vh"
    // width: 100%;
  },
  scoresRoot: {
    backgroundColor: "#634590",
    height: "15%",
    display: "flex",
    justifyContent: "space-around",
    alignItems: "center"
  },
  teamScoreContainer: {
    textAlign: "center",
    fontSize: "3rem",
    color: "white",
    fontWeight: "bold"
  }
});
