import React, { useState, useEffect } from "react";
import { makeStyles } from "@material-ui/styles";

const QCard = (props) => {
  const classes = useStyles();

  return (
    <div
      onClick={() => {
        if (props.isEnabled) {
          props.handleClicked(props.logicCIndex, props.logicQIndex);
        }
      }}
      className={classes.qcardRoot}
      style={
        props.isEnabled
          ? { backgroundColor: props.bgColor }
          : { backgroundColor: "grey" }
      }
    >
      <div>{props.score}</div>
    </div>
  );
};

const useStyles = makeStyles({
  qcardRoot: {
    height: "10%",
    display: "flex",
    alignItems: "center",
    justifyContent: "center",
    fontSize: "5rem",
    borderRadius: 20,
    color: "white",
    width: "90%",
    fontWeight: "bold",
    boxShadow: "8px 3px 21px -3px rgba(0,0,0,0.3)"
  },
  enabledCard: {
    background: "#068041"
  },
  disabledCard: {
    backgroundColor: "grey"
  }
});
export default QCard;
