import QCard from "./QCard";
import { makeStyles } from "@material-ui/styles";
import React, { useState, useEffect } from "react";

const GameBoard = (props) => {
  const classes = useStyles();
  const handleClickedCard = (catagoryIndex, qIndex) => {
    props.handleClick(catagoryIndex, qIndex);
  };
  return (
    <div className={classes.gameboardRoot}>
      {props.data.map((item, index) => {
        return (
          <div className={classes.categoryContainer} key={`item-${index}`}>
            <div className={classes.categoryTitleText}>{item.catagory}</div>
            <div className={classes.qcardsContainer}>
              {item.questions.map((question, i) => {
                return (
                  <QCard
                    key={`question-${index}${i}`}
                    score={question.score}
                    logicQIndex={i}
                    logicCIndex={index}
                    handleClicked={handleClickedCard}
                    isEnabled={question.isEnabled}
                    bgColor={question.color}
                  />
                );
              })}
            </div>
          </div>
        );
      })}
    </div>
  );
};

const useStyles = makeStyles({
  gameboardRoot: {
    // backgroundColor: "red",
    display: "flex",
    height: "85%",
    position: "relative"
  },
  categoryContainer: {
    // backgroundColor: "blue",
    width: "20%",
    height: "100%"
  },
  categoryTitleText: {
    color: "black",
    height: "10%",
    textAlign: "center",
    fontSize: "4rem",
    display: "flex",
    justifyContent: "center",
    alignItems: "center"
  },
  qcardsContainer: {
    // backgroundColor: "orange",
    position: "relative",
    height: "90%",
    display: "flex",
    flexDirection: "column",
    justifyContent: "space-around",
    alignItems: "center"
  }
});

export default GameBoard;
