import React, { useState, useEffect } from "react";
import { makeStyles } from "@material-ui/styles";
const QPage = (props) => {
  const [isValidatingChoice, setIsValidatingChoice] = useState(false);
  const [validationText, setValidationText] = useState(null);
  const [answer, setAnswer] = useState(null);
  const handleSetAnswerBeforeSend = (team, multi) => {
    setAnswer([team, multi]);
    let str = "";
    if (team === -1) {
      str = "אף אחד";
    } else {
      if (team === 0) {
        str = "קבוצה 1";
      } else {
        str = "קבוצה 2";
      }
      if (multi === 1) {
        str = `${str} מלאה`;
      } else {
        str = `${str} חלקית`;
      }
    }
    setValidationText(str);
    setIsValidatingChoice(true);
  };
  const handleValidationClick = (val) => {
    if (val) {
      if (answer[0] === -1) {
        props.handleClose(true);
      } else {
        props.handleCorrectAnswer(answer[0], answer[1]);
      }
    } else {
      setAnswer(null);
      setValidationText(null);
      setIsValidatingChoice(false);
    }
  };
  const classes = useStyles();
  return (
    <div className={classes.qpageRoot}>
      <div className={classes.catagoryTextContainer}>
        <div className={classes.catagoryText}>{props.categoryStr}</div>
      </div>
      <div className={classes.questionTextContainer}>
        <div className={classes.questionText}>{props.questionStr}</div>
      </div>
      <div className={classes.ScoresMainContainer}>
        {!isValidatingChoice ? (
          <div className={classes.buttonsMainContainer}>
            <div className={classes.buttonsContainer}>
              <button
                className={classes.btnStyle}
                onClick={() => handleSetAnswerBeforeSend(0, 1)}
              >
                קבוצה 1
              </button>
              <button
                className={classes.btnStyle}
                onClick={() => handleSetAnswerBeforeSend(0, 0.5)}
              >
                קבוצה 1 - תשובה חלקית
              </button>
            </div>
            <button
              className={classes.btnStyle}
              onClick={() => handleSetAnswerBeforeSend(-1, -1)}
            >
              אף אחד
            </button>
            <div className={classes.buttonsContainer}>
              <button
                className={classes.btnStyle}
                onClick={() => handleSetAnswerBeforeSend(1, 1)}
              >
                קבוצה 2
              </button>
              <button
                className={classes.btnStyle}
                onClick={() => handleSetAnswerBeforeSend(1, 0.5)}
              >
                קבוצה 2 - תשובה חלקית
              </button>
            </div>
          </div>
        ) : (
          <div className={classes.validationContainer}>
            <div
              className={classes.ScoresTitle}
              style={{ color: props.questionColor }}
            >
              {validationText}
            </div>
            <div>
              <button
                className={classes.btnStyle}
                onClick={() => handleValidationClick(true)}
              >
                אישור
              </button>
              <button
                className={classes.btnStyle}
                onClick={() => handleValidationClick(false)}
              >
                ביטול
              </button>
            </div>
          </div>
        )}
      </div>
    </div>
  );
};
const useStyles = makeStyles({
  qpageRoot: {
    height: "85%"
  },
  catagoryText: {
    color: "black",
    fontSize: "3rem",
    textAlign: "center"
  },
  ScoresTitle: {
    color: "black",
    fontSize: "3rem",
    fontWeight: "bold"
    // textAlign: "center"
  },
  questionText: { color: "black", fontSize: "5rem", textAlign: "center" },
  catagoryTextContainer: {
    height: "15%",
    display: "flex",
    alignItems: "center",
    justifyContent: "center"
  },
  questionTextContainer: {
    height: "55%",
    display: "flex",
    alignItems: "center",
    justifyContent: "center"
  },
  ScoresMainContainer: {
    height: "35%",
    display: "flex",
    flexDirection: "column",
    alignItems: "center",
    width: "100%"
  },

  ScoresTitleContainer: {
    display: "flex",
    alignItems: "center",
    justifyContent: "center"
  },
  buttonsMainContainer: {
    display: "flex",
    justifyContent: "space-around",
    width: "100%"
  },
  buttonsContainer: {
    display: "flex",
    flexDirection: "column"
  },
  btnStyle: {
    background: "linear-gradient(45deg, #A178DF 50%,#DCB0FF 90%)",
    border: 0,
    borderRadius: 3,
    boxShadow: "0 3px 5px 2px rgba(220, 176, 255, .3)",
    color: "white",
    fontSize: "2rem",
    margin: 10,
    padding: 20
  },
  validationContainer: {
    display: "flex",
    alignItems: "center",
    justifyContent: "center",
    flexDirection: "column"
  }
});
export default QPage;
